import React, { useState, useEffect } from "react";
import { MDBContainer } from "mdbreact";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLaptopCode } from "@fortawesome/free-solid-svg-icons";
import "./portfolio.scss";
import ProjectTemp from "./devProject";
import { projectDetails } from "./projectDetails";
import uuid from "uuid";
import { CSSTransition } from "react-transition-group";
import ProjectClear from "./Projectdescriptions";

export default function Portfolio() {
  const [gategory, setGategory] = useState(false);
  const [select, setSelect] = useState();
  const [devproject, setDevProject] = useState();
  const [change, setChange] = useState(false);

  useEffect(() => {
    if (select) {
      let project = projectDetails.filter(({ name }) => name === select);
      setDevProject(project);
    }
  }, [select]);

  const style = {
    background: "#78909C",
    boxShadow: "0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1)"
  };
  return (
    <MDBContainer className="portoflio">
      <h1>Portfolio</h1>
      <div className="CategoryCont">
        <span
          onClick={() => {
            setGategory(false);
            setSelect();
          }}
          style={!gategory ? style : { background: "none" }}
        >
          <p>
            <FontAwesomeIcon icon={faLaptopCode} />
          </p>
        </span>
      </div>
      <CSSTransition in={change} timeout={500} classNames="page">
        {!select ? (
          <div className="projectCont">
            {projectDetails.map(({ image, name }) => (
              <ProjectTemp
                key={uuid()}
                image={image}
                name={name}
                type={"Web Development"}
                select={() => {
                  setChange(true);
                  setTimeout(() => {
                    setSelect(name);
                  }, 300);
                }}
              />
            ))}
          </div>
        ) : (
          <React.Fragment>
            {devproject ? (
              devproject.map(
                ({
                  image,
                  name,
                  description,
                  techStacks,
                  year,
                  github,
                  link
                }) => (
                  <ProjectClear
                    key={uuid()}
                    image={image}
                    name={name}
                    description={description}
                    techStacks={techStacks}
                    year={year}
                    github={github}
                    link={link}
                    isVisible={select}
                    cancel={() => {
                      setChange(false);

                      setTimeout(() => {
                        setSelect();
                      }, 400);
                    }}
                  />
                )
              )
            ) : (
              <div className="spinner-border" role="status">
                <span className="sr-only">Loading...</span>
              </div>
            )}
          </React.Fragment>
        )}
      </CSSTransition>
    </MDBContainer>
  );
}
