import React, { useState } from "react";
import {  MDBBtn, MDBIcon } from "mdbreact";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import Axios from "axios";
import {HandelEroor, Objlength } from "./formHelper";
import InputForm from "./input";

export default function ContactForm() {
  const [value, setValue] = useState({});
  const [laoder, setLoader] = useState(false);
  const [success, setSuccess] = useState(false);


 
  const HandelSubmit = (e) => {
    e.preventDefault();
    if (HandelEroor(value).length === 0 && Objlength(value) === 4) {
      setLoader(true);
      Axios.post("https://portfoliob.herokuapp.com/api/mailcontact", value)
        .then(() => {
          setLoader(false);
          setSuccess(true);
        })
        .catch((err) => {
          setLoader(false);
        });
    }
  };

  const handleChange = (e) => {
    e.persist();
    setValue((value) => ({ ...value, [e.target.name]: e.target.value }));
  };

  return (
    <form className="forms" onSubmit={HandelSubmit}>
      <p className="h5 mb-4">fell free to contact me</p>
      {success ? (
        <span className="success-message">
          <p>
            Thank you for your trust we will contact you in less than 24 hours
          </p>
          <p>
            <FontAwesomeIcon icon={faCheckCircle} />
          </p>
        </span>
      ) : (
        <React.Fragment>
          <div className="grey-text">
          <InputForm errors={ HandelEroor(value)} handleChange={handleChange} />
          </div>
          <div className="text-center">
            <MDBBtn
              outline
              color="secondary"
              type="submit"
              disabled={HandelEroor(value).length > 0 || Objlength(value) !== 4}
            >
              {!laoder ? (
                <span>
                  send <MDBIcon far icon="paper-plane" className="ml-1" />
                </span>
              ) : (
                <div className="spinner-border" role="status">
                  <span className="sr-only">Loading...</span>
                </div>
              )}
            </MDBBtn>
          </div>
        </React.Fragment>
      )}
    </form>
  );
}
