import React from "react";
import { MDBInput } from "mdbreact";

const InputForm = ({errors,handleChange}) => {
   
  return (
    <React.Fragment>
      <div className={errors.includes("name") ? "cont-input" : ""}>
        <MDBInput
          label="You Name"
          name="name"
          onChange={handleChange}
          icon="user"
          type="text"
        />
        {errors.includes("name") && (
          <p className="errors">{"name"} field is required *</p>
        )}
      </div>
      <div className={errors.includes("email") ? "cont-input" : ""}>
        <MDBInput
          label="You Email"
          name="email"
          onChange={handleChange}
          icon="envelope"
          type="text"
        />
        {errors.includes("email") && (
          <p className="errors">{"email"} field is required *</p>
        )}
      </div>
      <div className={errors.includes("subject") ? "cont-input" : ""}>
        <MDBInput
          label="Subject"
          name="subject"
          onChange={handleChange}
          icon="tag"
          type="text"
        />
        {errors.includes("subject") && (
          <p className="errors">{"subject"} field is required *</p>
        )}
      </div>
      <div className={errors.includes("message") ? "cont-input" : ""}>
        <MDBInput
          label="You Message"
          name="message"
          onChange={handleChange}
          icon="pencil-alt"
          type="text"
        />
        {errors.includes("message") && (
          <p className="errors">{"message"} field is required *</p>
        )}
      </div>
    </React.Fragment>
  );
};

export default InputForm;
