import React from "react";
import "./App.css";
import Main from "./main/main";
import Head from "./NavBar/navbar";
import About from "./About/about";
import Skills from "./skills/skills";
import Portfolio from "./Portfolio";
import ScrollAnimation from "react-animate-on-scroll";
import { ParallaxProvider } from "react-scroll-parallax";
import Contact from "./contact";
import { Element } from "react-scroll";

function App() {
  return (
    <ParallaxProvider>
      <div className="App">
        <Head />
        <ScrollAnimation animateIn="fadeIn" animateOut="fadeOut">
        <Element name="main">
          <Main />
          </Element>
        </ScrollAnimation>
        <ScrollAnimation animateIn="fadeIn" animateOut="fadeIn">
          <Element name="about">
            {" "}
            <About />
          </Element>
        </ScrollAnimation>
        <ScrollAnimation
          animateIn="fadeIn"
          animateOut="fadeIn"
        >
          <Element name="skills">
            <Skills />
          </Element>
        </ScrollAnimation>
        <ScrollAnimation
        animateIn="fadeIn"
        animateOut="fadeIn"
      >
        <Element name="portfolio">
          <Portfolio />
        </Element>
        </ScrollAnimation>
        <ScrollAnimation
        animateIn="fadeIn"
        animateOut="fadeIn"
      >
        <Element name="contact">
          <Contact />
        </Element>
        </ScrollAnimation>
      </div>
    </ParallaxProvider>
  );
}

export default App;
