import React, { useState, useEffect } from "react";
import { MDBProgress } from "mdbreact";
import VizSensor from 'react-visibility-sensor';



const ProgressB = ({average}) => {
  const [progrssing, setProgressing] = useState(0);
  const [visible,setVisible] = useState(false)
  useEffect(()=>{
      if(visible){
        setTimeout(()=>{
            setProgressing(average)
        },1000)
      }
     else{
         setProgressing(0)
     }
 
  },[average,visible])

  return (
    <VizSensor onChange={(isVisible)=> setVisible(isVisible)} >
    <MDBProgress  value={progrssing} className="my-2" color="blue-grey" />
    </VizSensor>  
    )
};

export default ProgressB