import React, { useState } from "react";



export default function ProjectTemp({
  image,
  name,
type,
select
}) {
  const [Show, setShow] = useState(false);

  return (
    <div className="devProject" onClick={select}>
      <div
        className="project"
        onMouseEnter={() => {
          setShow(true);
        }}
        onMouseLeave={() => {
          setShow(false);
        }}
      >
        <img
          src={image}
          alt={name}
          style={{ filter: Show ? "blur(8px)" : null }}
        />
        {Show ? (
          <div className="ProjectInfo">
            <div>
              <p>{name}</p>
              <p>{type}</p>
            </div> 
          </div>
        ) : null}
      </div>
    </div>
  );
}
