import React from "react";
import { MDBBtn } from "mdbreact";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faGithub,
} from "@fortawesome/free-brands-svg-icons";

export default function ProjectClear({
  image,
  name,
  description,
  techStacks,
  year,
  github,
  link,
  cancel
}) {
  return (
    <div className="projectDetails">
      <img src={image} alt={name} />
      <div className="projectInfo">
        <section>
          <h1>{name}</h1>
        </section>
        <section>
          <p>Description:</p>
          <p>{description}</p>
        </section>
        <section>
          <p>Tech Stack:</p>
          <p>{techStacks}</p>
        </section>
        <section>
          <p>Year:</p>
          <p>{year}</p>
        </section>
        <section>
          <p>github:</p>
          {github === "private" ? (
            <p>Private</p>
          ) : (
        <div className="link"><FontAwesomeIcon  icon={faGithub} /> <a href={github}>source code</a></div>   
          )}
        </section>
        <section>
        <a href={link}><MDBBtn color="blue-grey">Live demo</MDBBtn></a>
        <MDBBtn outline onClick={cancel}>cancel</MDBBtn>
        </section>
      </div>
    </div>
  );
}
