

export const  HandelEroor = (obj)  => {
  let object = Object.keys(obj).map((key, idx) => {
    if (obj[key].length === 0) {
      return key;
    }
    return false;
  });

  let filtred = object.filter((val) => {
    return val;
  });

  return filtred;
}

export const  Objlength = (obj) => {
  let size = 0;
  for (let key in obj) {
    if (obj.hasOwnProperty(key)) size++;
  }
  return size;
}
