import React, { useState ,useEffect} from "react";
import {
  MDBNavbar,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavbarBrand
} from "mdbreact";
import "./navbar.scss";
import { Link } from "react-scroll";

const Head = () => {
  const [toggole, setToggle] = useState(false);
  const [navbg,setBgColor] = useState(false)

  const ChangeNavbg = () =>{
    if(window.scrollY > 250){
      setBgColor(true)
    }
    else if(window.scrollY < 250){
      setBgColor(false)
    }
  }

  useEffect(()=>{
    window.addEventListener("scroll",ChangeNavbg)
    return () =>{
      window.removeEventListener("scroll",ChangeNavbg)
    }
  })

  return (
    <MDBNavbar dark expand="md" fixed="top" className={!navbg ?"navBlack" : "navBlack2"}>
      <MDBNavbarBrand className={!navbg ? "navBarnone" : null}>
       <Link smooth duration={500} to="main"><strong>Ragheb Ben Amor</strong></Link> 
      </MDBNavbarBrand>
      <MDBNavbarToggler
        onClick={() => {
          setToggle(!toggole);
        }}
      />
      <MDBCollapse id="navbarCollapse3" isOpen={toggole} navbar>
        <MDBNavbarNav right>
          <MDBNavItem>
            <Link to="about" smooth duration={500}>
              About
            </Link>
          </MDBNavItem>
          <MDBNavItem>
            <Link to="skills" smooth duration={500}>
              Skills
            </Link>
          </MDBNavItem>
          <MDBNavItem>
            <Link to="portfolio" smooth duration={500}>
              Portfolio
            </Link>
          </MDBNavItem>
          <MDBNavItem>
            <Link to="contact" smooth duration={500}>
              Contact
            </Link>
          </MDBNavItem>
        </MDBNavbarNav>
      </MDBCollapse>
    </MDBNavbar>
  );
};

export default Head;
