import React from "react";
import { Link } from "react-scroll";

const Aboutme = () => {
  return (
    <div className="aboutme">
      <h2>About</h2>
      <p>
        Hello! I am Ragheb, web developer and web designer I have been working
        as a freelance web developer for more than two years now building web
        apps(static/dynamic/progressive) and I am working with MERN Stack now.
        I have been also designing websites prototype for almost one year now so
        if you need your business to grow up you need a modern web app so
        contact me <Link to="contact">here</Link>
      </p>
    </div>
  );
};

export default Aboutme;
